export function getAllUsers(token) {
  return request({
    headers: {
      "Content-Type": "application/json",
      Accept: "application/json",
      Authorization: `Bearer ${token}`
    },
    url: "/v1/user",
    method: "get"
  });
}

//update user
export function updateUser(token, id, data) {
  return request({
    headers: {
      "Content-Type": "application/json",
      Accept: "application/json",
      Authorization: `Bearer ${token}`
    },
    url: `/v1/user/${id}`,
    method: "patch",
    data
  });
}

