<template>
  <div class="app-container">
    <!-- User Customer Start -->
    <div v-if="user.role == 'customer'" class="relative flex justify-center flex-col min-h-screen">
      <div class="flex justify-center p-5 absolute top-0 left-0 w-full">
        <router-link to="#" class="mr-auto">
          <img src="@/assets/icons/settings.svg" class="menu-icon" />
        </router-link>
        <span class="font-semibold text-xl text-white tracking-wide capitalize">Scan &amp; Pay</span>
        <router-link to="#" class="ml-auto">
          <img src="@/assets/icons/menu-user.svg" class="menu-icon" />
        </router-link>
      </div>

      <div class="img-wrapper p-5 text-center">
        <div class="inline-block relative p-1">
          <div class="qr-borders qr-top-left absolute top-0 left-0 absolute w-full h-full"></div>
          <div class="qr-borders qr-bottom-right bottom-0 right-0 absolute w-full h-full"></div>
          <img src="@/assets/img/qr.png" class="mx-auto qr-image" />
        </div>
      </div>

      <div class="flexjustify-center items-center absolute left-0 bottom-0 p-5 w-full">
        <button
          @click="scanQR()"
          class="tracking-wide w-full p-3 px-5 border border-pink-700 rounded-lg h-16 text-xl placeholder-black focus:border-teal-500 focus:outline-none tracking-wide qr-btn text-white"
        >SCAN</button>
      </div>
    </div>
    <!-- User Customer End-->
  </div>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "Home",
  data() {
    return {
      user: { role: null },
      loading: ""
    };
  },
  computed: {
    ...mapGetters(["name"])
  },
  created() {
    this.user.role = "customer";
  },
  methods: {
    async logout() {
      await this.$store.dispatch("user/logout");
      this.$router.push(`/login?redirect=${this.$route.fullPath}`);
    },
    scanQR() {
      let self = this;
      /* eslint-disable */
      cordova.plugins.barcodeScanner.scan(
        function(result) {
          let text = `Scan successful. Data ${result.text}`;
          self.$msg.success({
            message: text,
            width: "80%",
            position: "bottom-center"
          });
        },
        function(error) {
          let text = `Scanning failed: ${error}`;
          self.$msg.error({
            message: text,
            width: "80%",
            position: "bottom-center"
          });
        },
        {
          preferFrontCamera: false,
          showFlipCameraButton: true,
          showTorchButton: true,
          saveHistory: true,
          prompt: "Place the QR Code inside the scan area", // Android
          resultDisplayDuration: 500,
          orientation: "portrait"
        }
      );
    }
  }
};
</script>

