  background: linear-gradient(
    90deg,
    rgba(156, 56, 216, 1) 0%,
    rgba(252, 110, 152, 1) 100%
  );
}

.qr-borders {
  &::after,
  &::before {
    content: "";
    position: absolute;
    background: #999;
  }
}

.qr-top-left {
  &:before {
    top: 0;
    left: 0;
    width: 25%;
    height: 4px;
  }
  &:after {
    top: 0;
    left: 0;
    width: 4px;
    height: 25%;
  }
}

.qr-bottom-right {
  &:before {
    bottom: 0;
